<template>
  <div class="goods" id="goods" name="goods">
    <!-- 面包屑 -->
    <!-- <div class="breadcrumb">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item to="/">首页</el-breadcrumb-item>
        <el-breadcrumb-item>全部商品</el-breadcrumb-item>
        <el-breadcrumb-item v-if="search">搜索</el-breadcrumb-item>
        <el-breadcrumb-item v-else>分类</el-breadcrumb-item>
        <el-breadcrumb-item v-if="search">{{search}}</el-breadcrumb-item>
      </el-breadcrumb>
    </div> -->
    <!-- 面包屑END -->

    <!-- 分类标签 -->
    <div class="nav">
      <div class="product-nav">
        <el-tabs   @tab-click="handleClick" value="first">
          <el-tab-pane label="店铺商品" name="first"> </el-tab-pane>
          <el-tab-pane label="店铺订单" name="second"> </el-tab-pane>
          <el-tab-pane label="店铺详情" name="third"> </el-tab-pane>
         </el-tabs>
      </div>
    </div>
    <!-- 分类标签END -->

    <!-- 主要内容区 -->
    <div class="main" v-show="isshow">
      <div class="list">
        <MyList :list="product" v-if="product.length>0" ></MyList>
        <div v-else class="none-product">抱歉没有找到相关的商品，请看看其他的商品</div>
      </div>
      <!-- 分页 -->
      <div class="pagination">
        <el-pagination
          background
          layout="prev, pager, next"
          :page-size="pageSize"
          :total="total"
          @current-change="currentChange"
        ></el-pagination>
      </div>
      <!-- 分页END -->
    </div>

    <div class="main" v-show="ordershow">
    <div class="order">
    <!-- 我的订单主要内容 -->
    <div class="order-content" v-if="orders!==null">
      <div class="content" v-for="(item,index) in orders" :key="index">
          <ul>
            <!-- 我的订单表头 -->
            <li class="order-info">
              <div class="order-id">订单编号: {{orders[index].order_id}}</div>
              <div class="order-time">订单时间: {{orders[index].createTime | dateFormat}}</div>
              
            </li>
            <li class="header">
              <div class="pro-img"></div>
              <div class="pro-name">商品名称</div>
              <div class="pro-price">单价</div>
              <div class="pro-num">数量</div>
              <div class="pro-total">小计</div>
              <div class="pro-operate">操作</div>
  
            </li>
            <!-- 我的订单表头END -->

            <!-- 订单列表 -->
            <li class="product-list"  >
              <div class="pro-img">
                <router-link :to="{ path: '/goods/details', query: {productID:orders[index].goodsId} }">
                  <img :src="orders.goodsCoverImg" />
                </router-link>
              </div>
              <div class="pro-name">
                <router-link
                  :to="{ path: '/goods/details', query: {productID:orders[index].goodsId} }"
                >{{orders[index].goodsName}}</router-link>
              </div>
              <div class="pro-price">{{orders[index].sellingPrice}}元</div>
              <div class="pro-num">{{orders[index].goodsCount}}</div>
              <div class="pro-total pro-total-in">{{orders[index].sellingPrice*orders[index].goodsCount}}元</div>
            
              <div class="pro-operate">
                <el-button type="primary" size="mini" v-show="orders[index].orderStatus == 0">拒绝</el-button>
                <el-button type="success" size="mini" v-show="orders[index].orderStatus == 1">退款</el-button>
                <el-button type="warning" size="mini" v-show="orders[index].orderStatus == 1">发货</el-button>
                <!-- <el-button type="danger" size="mini" v-show="orders[index].orderStatus == 3">退货</el-button> -->
                <p v-show="orders[index].orderStatus == 2">等待签收</p>
                <p v-show="orders[index].orderStatus == 3">订单完成</p>
              </div>
            </li>
          </ul>
            <div class="order-bar">
              <div class="order-bar-right">
                <span>
                  <span class="total-price-title">状态：</span>
                  <span class="total-price">{{orderst[orders[index].orderStatus]}}  </span>
                </span>
              </div>
            </div>
          </div>
        </div>
          <!-- 订单列表END -->
    </div>
  </div>
    <!-- 主要内容区END -->
  </div>
</template>
<script>
// import MyMarkdown from "../components/MyMarkdown";
import 'element-ui/lib/theme-chalk/display.css';
export default {
  components: {
   },
   data(){
     return{
       pageSize:15,
       total:0,
       currentPage:0,
       isshow:true,
       ordershow:false,
             orderst:["待支付","已支付","已发货","已签收"],

       orders:"",
         product:"",
     }
   },
   created(){
     this.getData()
   },
    methods: {
          // 页码变化调用currentChange方法
      currentChange(currentPage) {
      this.currentPage = currentPage;
       
      },
 
      handleClick(tab, event) {
        console.log(tab, event);
        console.log(tab.index);
        if(tab.index==0){
        this.isshow = true
        this.ordershow = false
        }
        else if(tab.index==1){
        this.isshow = false
        this.ordershow = true;
        console.log("返回给");
        this.indexorder();
        }
        else{
          console.log("error");
        }
         console.log("this.isshow",this.isshow);
         console.log(" isshow",this.orders);
      },
      getData() {
      // 如果分类列表为空则请求全部商品数据，否则请求分类商品数据
        const api = "/goods"; //请求分类数据
       this.$axios(   
            {
              url: this.$target+api, 
              // url: "http://localhost:3000/cart", 
              method: 'get',
              params:{
 
              currentPage: this.currentPage,
              pageSize: this.pageSize
              }
        })
        .then(res => {
           this.product = res.data.data;
        //  this.total = res.data.total;
        })
        .catch(err => {
          return Promise.reject(err);
        }); 
    },
    indexorder(){
      let as = this.$store.getters.getUserId;
      console.log("付出和公交");
      this.$axios(   
            {
            //  url: "http://localhost:3000/cart", 
           url: this.$target+ "/orders",
              method: 'get',
              params:{
                userId:as
              }
        })
        .then(res => {
          console.log("法规规范",res.data.data);
          if(res.data.flag){
              this.orders = res.data.data;
              console.log(this.orders);
            }
            else{
              this.notifyError("获取所有订单",res.data.msg);
            }
          })
        .catch(err => {
          return Promise.reject(err);
        }); 
    }
    }

};
</script>
<style scoped>
.goods {
  background-color: #f5f5f5;
}
/* 面包屑CSS */
.el-tabs--card .el-tabs__header {
  border-bottom: none;
}
.goods .breadcrumb {
  height: 50px;
  background-color: white;
}
.goods .breadcrumb .el-breadcrumb {
  width: 1225px;
  line-height: 30px;
  font-size: 16px;
  margin: 0 auto;
}
/* 面包屑CSS END */

/* 分类标签CSS */
.goods .nav {
  background-color: white;
}
.goods .nav .product-nav {
  width: 1225px;
  height: 40px;
  line-height: 40px;
  margin: 0 auto;
}
.nav .product-nav .title {
  width: 50px;
  font-size: 16px;
  font-weight: 700;
  float: left;
}
/* 分类标签CSS END */

/* 主要内容区CSS */
.goods .main {
  margin: 0 auto;
  max-width: 1225px;
}
.goods .main .list {
  min-height: 650px;
  padding-top: 14.5px;
  margin-left: -13.7px;
  overflow: auto;
}
.goods .main .pagination {
  height: 50px;
  text-align: center;
}
.goods .main .none-product {
  color: #333;
  margin-left: 13.7px;
}
/* 主要内容区CSS END */

/* 我的订单头部CSS END */
.order .content {
  width: 1225px;
  margin: 0 auto;
  background-color: #fff;
  margin-bottom: 50px;
}

.order .content ul {
  background-color: #fff;
  color: #424242;
  line-height: 85px;
}
/* 我的订单表头CSS */
.order .content ul .order-info {
  height: 60px;
  line-height: 60px;
  padding: 0 26px;
  color: #424242;
  border-bottom: 1px solid #ff6700;
}
.order .content ul .order-info .order-id {
  float: left;
  color: #ff6700;
}
.order .content ul .order-info .order-time {
  float: right;
}

.order .content ul .header {
  height: 85px;
  padding-right: 26px;
  color: #424242;
}
/* 我的订单表头CSS END */

/* 订单列表CSS */
.order .content ul .product-list {
  height: 85px;
  padding: 15px 26px 15px 0;
  border-top: 1px solid #e0e0e0;
}
.order .content ul .pro-img {
  float: left;
  height: 85px;
  width: 120px;
  padding-left: 80px;
}
.order .content ul .pro-img img {
  height: 80px;
  width: 80px;
}
.order .content ul .pro-name {
  float: left;
  width: 200px;
}
.order .content ul .pro-name a {
  color: #424242;
}
.order .content ul .pro-name a:hover {
  color: #ff6700;
}
.order .content ul .pro-price {
  float: left;
  width: 50px;
  padding-right: 18px;
  text-align: center;
}
.order .content ul .pro-operate {
  float: left;
  width: 200px;
  padding-left: 100px;
  text-align: center;
}
.order .content ul .pro-num {
  float: left;
  width: 190px;
  text-align: center;
}
.order .content ul .pro-total {
  float: left;
  width: 160px;
  padding-right: 81px;
  text-align: right;
}
.order .content ul .pro-total-in {
  color: #ff6700;
}

.order .order-bar {
  width: 1185px;
  padding: 0 20px;
  border-top: 1px solid #ff6700;
  height: 50px;
  line-height: 50px;
  background-color: #fff;
}
.order .order-bar .order-bar-left {
  float: left;
}
.order .order-bar .order-bar-left .order-total {
  color: #757575;
}
.order .order-bar .order-bar-left .order-total-num {
  color: #ff6700;
}
.order .order-bar .order-bar-right {
  float: right;
}
.order .order-bar .order-bar-right .total-price-title {
  color: #ff6700;
  font-size: 14px;
}
.order .order-bar .order-bar-right .total-price {
  color: #ff6700;
